/**
 * 
 */
import React from 'react';
//  antd 
import { Form, Input, InputNumber, Select, Button, message } from 'antd';
//  prop-types
import PropTypes from 'prop-types';
//  fetch
import request from '../utils/request';

const FormItem = Form.Item;

const formLayout = {
  labelCol: {
    span: 4
  },
  wrapperCol: {
    span: 16
  }
};

class UserEditor extends React.Component {
  // --
  componentDidMount(){
    /**
     * componentWillMountform.setFieldsValue
     * componentDidMount
     */
    const { editTarget, form } = this.props;
    if(editTarget){
      // editTarget
      form.setFieldsValue(editTarget);
    }
  }

  // 
  handleSubmit(e){
    // submit
    e.preventDefault();
    // 
    const { form, editTarget } = this.props; // 

    // 
    form.validateFields((err, values) => {
      if(!err){
        // 
        let editType = '';
        let apiUrl = 'http://localhost:8000/user';
        let method = 'post';
        // 
        if(editTarget){
          editType = '';
          apiUrl += '/' + editTarget.id;
          method = 'put';
        }

        // 
        request(method,apiUrl,values)
          // 
          .then((res) => {
            // ,jsonid
            // res.id
            if(res.id){
              message.success(editType + '!');
              // 
              this.context.router.push('/user/list');
              return;
            }else{
              message.error(editType + '!');
            }
          })
          // 
          .catch((err) => console.error(err));
      }else{
        message.warn(err);
      }
    });
  }
  
  render() {
    // 
    const { form } = this.props;
    const { getFieldDecorator } = form;
    return (
      <div style={{width: '400'}}>
        <Form onSubmit={(e) => this.handleSubmit(e)}>
          <FormItem label=":" {...formLayout}>
            {getFieldDecorator('name',{
              rules: [
                {
                  required: true,
                  message: ''
                },
                {
                  pattern: /^.{1,4}$/,
                  message: '4'
                }
              ]
            })(
              <Input type="text" />
            )}
          </FormItem>

          <FormItem label=":" {...formLayout}>
            {getFieldDecorator('age',{
              rules: [
                {
                  required: true,
                  message: '',
                  type: 'number'
                },
                {
                  min: 1,
                  max: 100,
                  message: '1~100',
                  type: 'number'
                }
              ]
            })(
              <InputNumber />
            )}
          </FormItem>

          <FormItem label=":" {...formLayout}>
            {getFieldDecorator('gender',{
              rules: [
                {
                  required: true,
                  message: ''
                }
              ]
            })(
              <Select placeholder="">
                <Select.Option value="male"></Select.Option>
                <Select.Option value="female"></Select.Option>
              </Select>
            )}
          </FormItem>

          <FormItem wrapperCol={{...formLayout.wrapperCol, offset: formLayout.labelCol.span}}>
            <Button type="primary" htmlType="submit"></Button>
          </FormItem>
        </Form>
      </div>
    );
  }
}

// UserEditorroutercontextTypes
// this.context.routerReact Router
UserEditor.contextTypes = {
  router: PropTypes.object.isRequired
};

/**
 * Form.create({ ... })(UserEditor)UserEditorprops.form
 * props.form
 */
UserEditor = Form.create()(UserEditor);

export default UserEditor;


