/**
 * 
 */
import React from 'react';
//  antd 
import { Input, InputNumber, Form, Button, message } from 'antd';
//  prop-types
import PropTypes from 'prop-types';
// 
import AutoComplete from '../components/AutoComplete'; //  './AutoComplete'
//  fetch
import request,{get} from '../utils/request';

// const Option = AutoComplete.Option;
const FormItem = Form.Item;
// 
const formLayout = {
  // label  <Col> 
  labelCol: {
    span: 4
  },
  wrapperCol: {
    span: 16
  }
};

class BookEditor extends React.Component {
  // 
  constructor(props) {
    super(props);
  
    this.state = {
      recommendUsers: []
    };
    // this
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleOwnerIdChange = this.handleOwnerIdChange.bind(this);
  }

  // --
  componentDidMount(){
    /**
     * componentWillMountform.setFieldsValue
     * componentDidMount
     */
    const {editTarget, form} = this.props;
    if(editTarget){
      form.setFieldsValue(editTarget);
    }
  }

  // 
  handleSubmit(e){
    // submit
    e.preventDefault();
    // 
    const { form, editTarget } = this.props; // 
    // 
    form.validateFields((err, values) => {
      if(err){
        message.warn(err);
        return;
      }

      // 
      let editType = '';
      let apiUrl = 'http://localhost:8000/book';
      let method = 'post';
      // 
      if(editTarget){
        editType = '';
        apiUrl += '/' + editTarget.id;
        method = 'put';
      }

      // 
      request(method,apiUrl,values)
        // 
        .then((res) => {
          // ,jsonid
          // res.id
          if(res.id){
            message.success(editType + '!');
            // 
            this.context.router.push('/book/list');
          }else{
            message.error(editType + '!');
          }
        })
        // 
        .catch((err) => console.error(err));
    });    
  }

  // 
  getRecommendUsers (partialUserId) {
    // 
    get('http://localhost:8000/user?id_like=' + partialUserId)
    .then((res) => {
      if(res.length === 1 && res[0].id === partialUserId){
        // 1idid,id,
        return;
      }

      // 
      this.setState({
        recommendUsers: res.map((user) => {
          return {
            text: `${user.id}(${user.name})`,
            value: user.id
          }
        })
      });
    })
  }

  // 
  timer = 0;
  handleOwnerIdChange(value){
    this.setState({
      recommendUsers: []
    });

    // "",
    if(this.timer){
      // 
      clearTimeout(this.timer);
    }

    if(value){
      // 2001
      this.timer = setTimeout(() => {
        // 
        this.getRecommendUsers(value);
        this.timer = 0;
      }, 200);
    }
  }
  
  render() {
    // 
    const {recommendUsers} = this.state;
    const {form} = this.props;
    const {getFieldDecorator} = form;

    return (
      <Form onSubmit={this.handleSubmit} style={{width:'400'}}>
        <FormItem label=":" {...formLayout}>
          {getFieldDecorator('name',{
            rules: [
              {
                required: true,
                message: ''
              }
            ]
          })(
            <Input type="text" />
          )}
        </FormItem>

        <FormItem label=":" {...formLayout}>
          {getFieldDecorator('price',{
            rules: [
              {
                required: true,
                message: '',
                type: 'number'
              },
              {
                min: 1,
                max: 99999,
                type: 'number',
                message: '1~99999'
              }
            ]
          })(
            <InputNumber />
          )}
        </FormItem>

        <FormItem label=":" {...formLayout}>
          {getFieldDecorator('owner_id',{
            rules: [
              {
                required: true,
                message: 'ID'
              },
              {
                pattern: /^\d*$/,
                message: 'ID'
              }
            ]
          })(
            <AutoComplete
              options={recommendUsers}
              onChange={this.handleOwnerIdChange}
            />
          )}
        </FormItem>

        <FormItem wrapperCol={{span: formLayout.wrapperCol.span, offset: formLayout.labelCol.span}}>
          <Button type="primary" htmlType="submit"></Button>
        </FormItem>
      </Form>
    );
  }
}

// BookEditorroutercontextTypes
// this.context.routerReact Router
BookEditor.contextTypes = {
  router: PropTypes.object.isRequired
};

BookEditor = Form.create()(BookEditor);

export default BookEditor;


