/**
 * 
 */
import React from 'react';
//  antd 
import { message, Table, Button, Popconfirm } from 'antd';
//  prop-types
import PropTypes from 'prop-types';
//  fetch
import { get, del } from '../utils/request'; 

class BookList extends React.Component {
  // 
  constructor(props) {
    super(props);
    // 
    this.state = {
      bookList: []
    };
  }

  /**
   * 
   * componentWillMount
   * 
   */
  componentWillMount(){
    // 
    get('http://localhost:8000/book')
      .then((res) => {
        /**
         * 
         * 
         */
        this.setState({
          bookList: res
        });
      });
  }

  /**
   * 
   */
  handleEdit(book){
    // 
    this.context.router.push('/book/edit/' + book.id);
  }

  /**
   * 
   */
  handleDel(book){
    // 
    del('http://localhost:8000/book/' + book.id, {
    })
      .then(res => {
        /**
         * 
         * array.filter
         * Array
         */
        this.setState({
          bookList: this.state.bookList.filter(item => item.id !== book.id)
        });
        message.success('');
      })
      .catch(err => {
        console.error(err);
        message.error('');
      });
  }

  render() {
    // 
    const { bookList } = this.state;
    // antdTablecolumns
    const columns = [
      {
        title: 'ID',
        dataIndex: 'id'
      },
      {
        title: '',
        dataIndex: 'name'
      },
      {
        title: '',
        dataIndex: 'price',
        render: (text, record) => <span>&yen;{record.price / 100}</span>
      },
      {
        title: 'ID',
        dataIndex: 'owner_id'
      },
      {
        title: '',
        render: (text, record) => (
          <Button.Group type="ghost">
            <Button size="small" onClick={() => this.handleEdit(record)}></Button>
            <Popconfirm
              title="?"
              okText=""
              cancelText=""
              onConfirm={() => this.handleDel(record)}>
              <Button size="small"></Button>
            </Popconfirm>
          </Button.Group>
        )
      }
    ];

    return (
      <Table columns={columns} dataSource={bookList} rowKey={row => row.id} />
    );
  }
}

/**
 * this.context.xxxcontextTypesPropTypes
 */
BookList.contextTypes = {
  router: PropTypes.object.isRequired
};

export default BookList;


